<template>
    <div v-if="ready">
        <div class="upper">

        </div>
        <div v-if="hv_data" class="show_list">
            <table class="table table-striped first-td-padding">
                <thead>
                    <tr style="background-color:rgb(238,28,37)">
                    <th class="proc_th" style="width:3.5%">Action</th>
                    <th class="proc_th" style="width:8.5%">Procurement ID</th>
                    <th class="proc_th" style="width:8%">Asset Category</th>
                    <th class="proc_th" colspan="2">Display Title</th>
                    <th class="proc_th" style="width:8vw!important">Status</th>
                    <th class="proc_th" style="width:8%">Comparison Table</th>
                    <th class="proc_th" style="width:12%">Phase</th>
                    <th class="proc_th" style="width:4%">Type</th>
                    <th class="proc_th" style="width:10%">Related File(s)</th>
                    </tr>
                </thead>
                <tbody v-for="l in data.length">
                    <!--Cat2-->
                    <tr v-if="data[l-1].category=='2'" class="head_bar cat2">       <!--Collapse Row-->
                        <td class="proc_td" style="border-left:2px solid #ffffff;text-align:center;cursor:pointer;padding-top:0.4em">
                            <i class="fa fa-caret-down" style="transform:rotate(0deg);font-size:14px"></i>
                        </td>
                        <td class="proc_td" style=";text-transform: uppercase;">{{ data[l-1].proc_id }}</td>
                        <td class="elli_td proc_td">{{ data[l-1].asset_code }}</td>
                        <td class="elli_td proc_td" colspan="2">{{ data[l-1].display_title }}</td>
                        <td :class="'proc_td '+data[l-1].stat">{{ data[l-1].stat }}</td>
                        <td class="proc_td"><u style="cursor:pointer">View</u></td>          
                        <td class="proc_td">{{ data[l-1].phase }}</td>
                        <td class="proc_td">{{ data[l-1].reimb_type }}</td>
                        <td class="proc_td">Expand</td>
                    </tr>
                    <!--END Cat2-->
                    <!--Cat3-->
                    <tr v-if="data[l-1].category=='3'" class="head_bar cat3">       <!--Collapse Row-->
                        <td class="proc_td" style="border-left:2px solid #ffffff;text-align:center;cursor:pointer;padding-top:0.4em">
                            <i class="fa fa-caret-down" style="transform:rotate(0deg);font-size:14px"></i>
                        </td>
                        <td class="proc_td" style=";text-transform: uppercase;">{{ data[l-1].proc_id }}</td>
                        <td class="elli_td proc_td">{{ data[l-1].asset_code }}</td>
                        <td class="elli_td proc_td" colspan="2">{{ data[l-1].display_title }}</td>
                        <td :class="'proc_td '+data[l-1].stat">{{ data[l-1].stat }}</td>
                        <td class="proc_td"><u style="cursor:pointer">View</u></td>          
                        <td class="proc_td">Open Date : {{ data[l-1].open_date }}</td>
                        <td class="proc_td">{{ data[l-1].reimb_type }}</td>
                        <td class="proc_td">Expand</td>
                    </tr>
                    <!--END Cat3-->
                    <!--Cat4-->
                    <tr v-if="data[l-1].category=='4'" class="head_bar cat4">       <!--Collapse Row-->
                        <td class="proc_td" style="border-left:2px solid #ffffff;text-align:center;cursor:pointer;padding-top:0.4em">
                            <i class="fa fa-caret-down" style="transform:rotate(0deg);font-size:14px"></i>
                        </td>
                        <td class="proc_td" style=";text-transform: uppercase;">{{ data[l-1].proc_id }}</td>
                        <td class="elli_td proc_td">{{ data[l-1].asset_code }}</td>
                        <td class="elli_td proc_td" colspan="2">{{ data[l-1].display_title }}</td>
                        <td :class="'proc_td '+data[l-1].stat">{{ data[l-1].stat }}</td>
                        <td class="proc_td"><u style="cursor:pointer">View</u></td>          
                        <td class="proc_td">Open Date : {{ data[l-1].open_date }}</td>
                        <td class="proc_td">{{ data[l-1].reimb_type }}</td>
                        <td class="proc_td">Expand</td>
                    </tr>
                    <!--[END] Cat4-->
                </tbody>
            </table>
        </div>
    </div>    
</template>
<script>
export default {
    data(){
        return {
            show_item:[],
            hv_data:false,
            data:[],
            ready:false,
        }
    },
    async created(){
        await this.fetch_data();
        this.ready = true;
    },
    methods:{
        async fetch_data(){
            await axios.get('Procurement/procurement/backend/print_proc_render.php')
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.data = response.data;
                            console.log(response.data);
                            this.hv_data = true;
                        } else {
                            this.hv_data = false;
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
    .cat2{
        color:rgba(255,255,255,0.85);
        line-height:1rem!important;
        font-family:arial;
        background-color:rgb(88,89,91)!important;
        font-size:14px!important;
    }
    .cat3{
        color:rgba(255,255,255,0.85);
        line-height:1rem!important;
        font-family:arial;
        background-color:rgb(209,173,133)!important;
        font-size:14px!important;
    }
    .cat4{
        color:rgba(255,255,255,0.85);
        line-height:1rem!important;
        font-family:arial;
        background-color:rgb(156, 10, 14)!important;
        font-size:14px!important;
    }
    .proc_td{
        border-left:0px solid #ffffff;
    }
    .proc_td:last-child{
        border-right:2px solid #ffffff;
    }
    .Completed{
        color:rgba(0, 255, 153,0.6)
    }
    .Rejected{
        color:red;
    }
    .Restart{
        color:red
    }
</style>


